<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('CloneField') }}
                        </h5>
                        <button v-on:click="ClosePopup()" type="button" class="close" data-original-title="Close"><em
                                class="fa fa-times"></em></button>

                    </div>
                    <div class="border p-3">
                        <DynamicForm lang="en" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></DynamicForm>
                        <div class="col-lg-6">
                            <div class="py-2">
                                <span class="mandatory"> {{ $t('MandatoryString') }} </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import DataService from '../../services/DataService.js';
import { DynamicForm } from 'tg-controls_cli/dist/myLib.common'


export default {
    components: { DynamicForm },

    props: {
        CustomFieldId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            DataLoaded: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [

                        {
                            astype: "TextField",
                            label: "Custom Field Name",
                            name: "CustomFieldName",
                            value: "",
                            placeholder: "Enter Custom Field Name",
                            validationRules: { "required": true },

                        },
                        {
                            astype: "TextField",
                            label: "Display Name",
                            name: "DisplayName",
                            placeholder: "Enter Display Name",
                            value: "",
                            validationRules: { "required": true },

                        }

                    ]
                },

            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success saveSendforApproval",
                    text: "<i class='fa fa-save pr-2'></i> Save",
                    //onClick: this.onSubmit
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i> Cancel",
                    onClick: this.ClosePopup
                }
            ],
        }
    },
    created: function () {
        this.DataLoaded = true;
    },
    methods: {

        ClosePopup() {
            var vm = this;
            vm.$parent.isShowCloneFieldPopup = false;
        },

        onSubmit(formfields, val) {
            var vm = this;
            let params = `custom_field_id=${vm.CustomFieldId}&customFieldDisplayName=${formfields.DisplayName}&customFieldName=${formfields.CustomFieldName}`;
            DataService.CloneCustomField(params).then((response) => {

                if (response.data == "NAME_EXIST") {

                    vm.ShowAlert(
                        vm.$t("Custom Field Name Already Exist"),
                        "warning",
                        vm.$t("Alert")
                    );

                }
                else if (response.data == "CLONE") {

                    vm.ShowAlert(
                        vm.$t("Clone Custom Field Successfully Done"),
                        "success",
                        vm.$t("Alert")
                    );
                    vm.ClosePopup();
                    vm.$parent.FetchData();
                    return;

                }
                else if (response.data == "DISPLAY_NAME_EXIST") {

                    vm.ShowAlert(
                        vm.$t("Display Name Already Exist"),
                        "warning",
                        vm.$t("Alert")
                    );

                }
                else {

                    vm.ShowAlert(
                        vm.$t("Something went wrong"),
                        "failure",
                        vm.$t("Alert")
                    );

                }

            })


            return;
        }

    }
}
</script>