function bindleftCommonSearchdropdown(instance,modulename, filtername,moduleId) {
        var vm = instance;
        if (filtername == "module") {
            var params = `moduleId=&userTypeId=1&requestFrom=&isCustomFieldEnable=1`;
            DataService.Modulenamedata(params)
                .then(function (response) {
                    var json = response.data.Data;                
                    if (json != null) {  
                            vm.leftSearchFields[1].listOptions =json.map((item) => {
                                const container = {};                    
                                container.name = item.module_name;
                                container.value = (item.module_id).toString();                    
                                return container;
                            });     
                                                        
                    }
                });
        }
        
        if (filtername == "subModule") {
          vm.leftSearchFields[2].listOptions=[];
          var urlParams = `moduleId=${moduleId}&userTypeId=1&requestFrom=&isCustomFieldEnable=1`;
          DataService.Modulenamedata(urlParams).then(response => {
              if (response.data.Data != null) {
                  vm.subModuleNameList = response.data.Data;
                  vm.subModuleNameList.forEach((item, index) => {
                    let obj = {
                        name: item.sub_module_name,
                        value: item.sub_module_id
                    };
                    vm.leftSearchFields[2].listOptions.push(obj);
                  })
              }
          })

        }
}
export default { buttons, leftsearchSchema, listActions,TableHeader, bindleftCommonSearchdropdown };


