
<template>
    <div class="my-popups">
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle  py-0">
                        <button type="button" class="close" @click="closePopup">
                            <em aria-hidden="true" class="fa fa-times"></em>
                        </button>
                        <h4 name="header" class="modal-title py-3">
                            Edit {{ CustomFieldArray.name }} {{ $t('Properties') }}
                        </h4>
                    </div>
                    <div class="listing py-3 px-3">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

import { DynamicForm } from 'tg-controls_cli/dist/myLib.common';
import DataService from '../../services/DataService.js';

export default {
    components: {
        DynamicForm
    },
    props: {

        FieldName: {
            type: String,
            required: true
        },
        CustomFieldArray: {
            type: Object,
            required: true
        },

        LanguageArray: {
            type: Array,
            required: true
        },


    },
    data() {
        return {
            lenghtOfLanguageDiv: null,
            finalJsonData: [],
            Lblname: '',
            myObj: [],
            IsRequired: null,
            displayName: '',
            ddlLanguage: '',
            LengthVal: '',
            DecimalVal: '',
            txtDescription: '',
            dummyArray: [],
            SelectedData: [],
            langSelectedData: [],
            displayNameTextData: [],
            diplayNameWithCulture: [],
            langCulture: [],
            languageCultureArray: [],
            displayNameData: '',
            FormSchema: [
                {
                    layoutType: "four",
                    Data: [
                        {
                            astype: "TextField",
                            label: 'Label',
                            name: "Label",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            validationRules: { "required": true },
                        },
                        {
                            astype: "TextField",
                            label: 'Pick List Option',
                            name: "PickListOption",
                            value: '',
                            visibility: false,
                            placeholder: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "NumericField",
                            label: 'Length',
                            name: "Length",
                            value: '',
                            placeholder: "",
                            visibility: true,
                            validationRules: { "required": true },

                        },
                        {
                            astype: "NumericField",
                            label: 'Decimal Places',
                            name: "DecimalPlaces",
                            value: '',
                            placeholder: "",
                            visibility: false,
                            validationRules: { "required": true },

                        },
                        {
                            astype: "CheckBoxField",
                            label: "Mark as Required",
                            name: "MarkasRequired",
                            value: '',
                            placeholder: "",
                            visibility: true,
                            config: {
                                options: [{ value: "1", name: "" }]
                            }
                        },
                        {
                            astype: "TextAreaField",
                            label: 'Description',
                            name: "Description",
                            value: '',
                            placeholder: "",
                            visibility: true,
                            validationRules: "",
                            config: {
                                rows: 3
                            }
                        }
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: 'Language',
                            name: "Language",
                            value: '',
                            placeholder: "",
                            chkIndex: 0,
                            visibility: false,
                            config: {
                                options: [],

                            },
                            validationRules: "required",
                        },
                        {
                            astype: "TextField",
                            label: 'Display Name',
                            name: "DisplayName",
                            value: '',
                            placeholder: "",
                            chktxtIndex: 0,
                            visibility: false,
                            validationRules: { "required": true, max: 250 },
                            config: {
                                showAddIcon: true,
                                onAddButtonClick: this.AddMultiLanguage
                            },
                        },

                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Update"
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closePopup
                }
            ]
        }

    },
    created: function () {
        var vm = this;
        vm.ShowHideFields()
        vm.BindLangDDL();
        vm.EditBindFornData()
    },
    methods: {
        // Bind edited form data into fields.
        EditBindFornData() {

            var vm = this;
            (vm.FormSchema)[0].Data[0].value = vm.CustomFieldArray.name;
            (vm.FormSchema)[0].Data[1].value = vm.CustomFieldArray.picklist_options != "undefind" ? vm.CustomFieldArray.picklist_options : '';
            (vm.FormSchema)[0].Data[2].value = vm.CustomFieldArray.length;
            (vm.FormSchema)[0].Data[3].value = vm.CustomFieldArray.decimal_places != "undefind" ? vm.CustomFieldArray.decimal_places : '';
            (vm.FormSchema)[0].Data[4].value = vm.CustomFieldArray.is_required == true ? "1" : '';
            (vm.FormSchema)[0].Data[5].value = vm.CustomFieldArray.description;

            var objCulture1 = {};
            vm.CustomFieldArray.location_json.forEach((item) => {
                objCulture1 = {};
                objCulture1.value = item.lang_id;
                vm.languageCultureArray.push(objCulture1);
                objCulture1 = {};
                objCulture1.value = item.localization_value;
                vm.languageCultureArray.push(objCulture1);
            })

            if (vm.CustomFieldArray.location_json.length != 0) {
                var lengthIndex = vm.languageCultureArray.length / 2;
                var count = 1;

                vm.languageCultureArray.forEach((item, index) => {
                    if (count < lengthIndex) {
                        (vm.FormSchema)[1].Data.push({
                            astype: "SelectField",
                            label: 'Language',
                            name: "Language" + (vm.FormSchema)[1].Data.length,
                            value: "",
                            placeholder: "",
                            chkIndex: (vm.FormSchema)[1].Data.length,
                            config: {
                                options: [],
                            },
                            validationRules: "required",
                        },
                            {
                                astype: "TextField",
                                label: 'Display Name',
                                name: "DisplayName" + (vm.FormSchema)[1].Data.length,
                                value: "",
                                placeholder: "",
                                chktxtIndex: (vm.FormSchema)[1].Data.length,
                                validationRules: { "required": true, max: 250 },
                                config: {
                                    showAddIcon: true,
                                    onaddIconBackground: 'bg-danger',
                                    onAddButtonClick: vm.RemoveFile,
                                    addIcon: 'fa fa-trash'
                                },
                            })

                        vm.BindLangDDL(vm.Lblname);
                        count++;
                    }

                })
                vm.languageCultureArray.forEach((itm, indx) => {
                    (vm.FormSchema)[1].Data[indx].value = itm.value;
                })

            }
        },
        
        // show hide controls on their data types
        ShowHideFields() {
            var vm = this;

            if (vm.CustomFieldArray.is_system_generated == true) {
                (vm.FormSchema)[0].Data[1].visibility = false;
                (vm.FormSchema)[0].Data[0].disabled = true;
                (vm.FormSchema)[0].Data[2].visibility = false;
                (vm.FormSchema)[0].Data[3].visibility = false;
                (vm.FormSchema)[0].Data[5].visibility = false;
                (vm.FormSchema)[1].Data[0].visibility = false;
                (vm.FormSchema)[1].Data[1].visibility = false;
            }
            else {
                (vm.FormSchema)[1].Data[0].visibility = true;
                (vm.FormSchema)[1].Data[1].visibility = true;
                if (vm.FieldName == 'Decimal') {
                    (vm.FormSchema)[0].Data[3].visibility = true;
                }
                if (vm.FieldName == 'Select List' || vm.FieldName == 'Checkbox') {
                    (vm.FormSchema)[0].Data[1].visibility = true;
                }

                if (vm.FieldName == 'Date') {
                    (vm.FormSchema)[0].Data[1].visibility = false;
                    (vm.FormSchema)[0].Data[3].visibility = false;
                }
            }
        },

        // Bind language dropdwon 
        BindLangDDL(name) {
            var vm = this;
            if (name == undefined) {
                vm.LanguageArray.forEach((itm, index) => {
                    (vm.FormSchema)[1].Data[0].config.options.push({
                        value: itm.lang_id,
                        name: itm.lang_name
                    })
                })
            }

            else {
                vm.LanguageArray.forEach((itm, index) => {
                    if (typeof (name) != 'undefined' && name != null) {
                        (vm.FormSchema)[1].Data.forEach((item, indx) => {
                            if (indx % 2 == 0) {
                                (vm.FormSchema)[1].Data[indx].config.options.push({
                                    value: itm.lang_id,
                                    name: itm.lang_name
                                })
                            }
                        })

                    }

                })
            }

        },

        closePopup() {

            var vm = this;
            vm.$parent.isShowEditCustomField = false;
            vm.$parent.FetchData();

        },

        // Add lang and display name fields on click of plus button
        AddMultiLanguage() {
            var vm = this;
            (vm.FormSchema)[1].Data.push({
                astype: "SelectField",
                label: 'Language',
                name: "Language" + (vm.FormSchema)[1].Data.length,
                value: "",
                placeholder: "",
                chkIndex: (vm.FormSchema)[1].Data.length,
                config: {
                    options: [],
                },
                validationRules: "required",
            },
                {
                    astype: "TextField",
                    label: 'Display Name',
                    name: "DisplayName" + (vm.FormSchema)[1].Data.length,
                    value: "",
                    placeholder: "",
                    chktxtIndex: (vm.FormSchema)[1].Data.length,
                    validationRules: { "required": true, max: 250 },
                    config: {
                        showAddIcon: true,
                        onaddIconBackground: 'bg-danger',
                        onAddButtonClick: vm.RemoveFile,
                        addIcon: 'fa fa-trash'
                    },
                })
            vm.FormSchema[1].Data.forEach((item) => {
                vm.Lblname = item.name
            })
            vm.BindLangDDL(vm.Lblname);
        },
        // Remove Element on click of delete button
        RemoveFile: function (e, field) {

            var vm = this;
            var checkindex = (vm.FormSchema)[1].Data.findIndex(x => x.chktxtIndex === field.chktxtIndex);
            var chechIndex1 = checkindex - 1;
            if (checkindex > -1) {
                (vm.FormSchema)[1].Data.splice(checkindex, 1);

            }
            if (chechIndex1 > -1) {
                (vm.FormSchema)[1].Data.splice(chechIndex1, 1);
            }

        },

        //Submit data in an array to maintain the property on click of save button 
        onSubmit(formfields, e) {

            var vm = this;
            vm.lenghtOfLanguageDiv = (vm.FormSchema)[1].Data;
            var objCulture = {};
            vm.lenghtOfLanguageDiv.forEach((itm, indx) => {
                if (itm.astype == "SelectField") {
                    objCulture.localization_id = 0;
                    objCulture.custom_field_id = 0;
                    objCulture.user_guide = '',
                        objCulture.lang_id = itm.value;
                }
                if (itm.astype == "TextField") {
                    objCulture.localization_value = itm.value;
                    vm.langCulture.push(objCulture)
                    objCulture = {};
                }
            })

            var SelectedData = ""
            if ((vm.FormSchema)[1].Data.length > 0) {
                (vm.FormSchema)[1].Data.forEach((item, indx) => {
                    if (indx % 2 != 0) {
                        SelectedData += item.value + '^'
                    }
                    else {
                        SelectedData += item.value + ','
                    }
                })
                var AllData = SelectedData.length - 1
                vm.displayNameData = SelectedData.slice(0, AllData);
            }
            if (vm.CustomFieldArray != undefined) {

                if ((vm.FormSchema)[0].Data[4].value.length != 0) {
                    vm.CustomFieldArray.is_required = (vm.FormSchema)[0].Data[4].value[0] != undefined ? true : false;
                }
                else {
                    vm.CustomFieldArray.is_required = '';
                }
                if (formfields.PickListOption != undefined) {
                    vm.CustomFieldArray.picklist_options = formfields.PickListOption;
                }
                if (formfields.Length != undefined) {
                    vm.CustomFieldArray.length = formfields.Length
                }
                if (vm.CustomFieldArray.group_id > 0) {
                    vm.CustomFieldArray.is_updated = 1;
                }
            }

            let mainObj = {
                CUSTOM_FIELD_ID: vm.CustomFieldArray.custom_field_id,
                MODULE_ID: vm.CustomFieldArray.module_id,
                SUB_MODULE_ID: vm.CustomFieldArray.sub_module_id,
                NAME: formfields.Label,
                DISPLAY_NAME: vm.CustomFieldArray.display_name,
                DESCRIPTION: vm.CustomFieldArray.Description,
                PICKLIST_OPTION: formfields.PickListOption,
                TYPE_ID: vm.CustomFieldArray.data_type_id,
                LENGTH: formfields.Length,
                IS_REQUIRED: formfields.MarkasRequired !== null && formfields.MarkasRequired !== undefined
                    ? formfields.MarkasRequired.toString() === '1' ? 1 : 0
                    : 0,
                STATUSID: 1001,
                DECIMAL_PLACES: formfields.DecimalPlaces,
                LOC_VALUE: vm.displayNameData,
                FORM_FIELD_VISIBILITY: 'YES',
                LIST_FIELD_VISIBILITY: 'YES'
            }
            vm.finalJsonData.push(mainObj);
            var params = `jsonString=${JSON.stringify(vm.finalJsonData)}`
            DataService.SaveUpdateCustomField(params).then(function (response) {
                var checkstatus = JSON.parse(response.data);
                if (checkstatus.STATUS_CODE == 1) {
                    vm.ShowAlert(vm.$t('CustomFieldSuccess', [vm.$t('Field')]), "success", true, vm.$t("Alert"));
                    vm.closePopup();
                }
                else if (checkstatus.STATUS_CODE == 3) {
                    vm.ShowAlert(vm.$t('CustomFieldExists'), "danger", true, vm.$t("Alert"));
                }
                else if (checkstatus.STATUS_CODE == 2) {
                    vm.ShowAlert(vm.$t('CustomFieldUpdate'), "success", true, vm.$t("Alert"));
                    vm.closePopup();
                }

            })
        }
    }

}

</script>