<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('AddModule') }}
                        </h5>
                        <button v-on:click="ClosePopup()" type="button" class="close" data-original-title="Close"><em
                                class="fa fa-times"></em></button>

                    </div>
                    <div class="border p-3">
                        <DynamicForm lang="en" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></DynamicForm>
                        <div class="col-lg-6">
                            <div class="py-2">
                                <span class="mandatory"> {{ $t('MandatoryString') }} </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>

import DataService from '../../services/DataService.js';
import { DynamicForm } from 'tg-controls_cli/dist/myLib.common';



export default {
    components: { DynamicForm },

    props: {
        CustomFieldId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            DataLoaded: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: "Module Name",
                            name: "ModuleName",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.getSubModuleByModuleId
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "SelectField",
                            label: "Sub Module Name",
                            name: "SubModuleName",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "required",
                        },

                    ]
                },

            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-info ",
                    text: "<i class='fa fa-save pr-2'></i> Next",
                    //onClick: this.onSubmit
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i> Cancel",
                    onClick: this.ClosePopup
                }
            ],
        }
    },
    created: function () {

        debugger;

        var vm = this;
        this.DataLoaded = true;
        vm.getModule();
    },
    methods: {

        getModule: function () {
            var vm = this;
            var params = `moduleId=&userTypeId=1&requestFrom=&isCustomFieldEnable=1`;
            DataService.Modulenamedata(params).then(response => {
                if (response.data.Data != null) {
                    vm.moduleNameList = response.data.Data;
                    vm.moduleNameList.forEach((item, index) => {
                        (vm.FormSchema)[0].Data[0].config.options.push({
                            value: item.module_id,
                            name: item.module_name
                        })
                    })
                }
            })
        },

        getSubModuleByModuleId(e, field, moduleId) {
            var vm = this;
            if (moduleId) {
                vm.ddlModuleId = moduleId;
            } else {
                vm.ddlModuleId = e.target.value;
            }
            (vm.FormSchema)[0].Data[1].config.options = [];
            var params = `moduleId=${vm.ddlModuleId}&userTypeId=1&requestFrom=&isCustomFieldEnable=1`;
            DataService.Modulenamedata(params).then(response => {
                if (response.data.Data != null) {
                    vm.subModuleNameList = response.data.Data;
                    vm.subModuleNameList.forEach((item, index) => {
                        (vm.FormSchema)[0].Data[1].config.options.push({
                            value: item.sub_module_id,
                            name: item.sub_module_name
                        })
                    })
                }
            })
        },


        ClosePopup() {

            debugger;
            var vm = this;
            vm.$parent.isShowModulePopup = false;
        },

        onSubmit(formfields, val) {
            debugger

            var vm = this;
            vm.$emit('openCustomField', formfields);



        }
    }
}
</script>